<template>
	<div class="">
		<v-navigation-drawer
            class="hidden-md-and-up"
            v-model="drawer"
            app
            clipped
            disable-resize-watcher
            elevate-on-scroll
            color="">
            <v-list dense>
                <v-list-item link v-for="(item, i) in items" :key="i"
                    @click="$vuetify.goTo(item.link, options)">
                    <v-list-item-action>
                        <v-icon>{{ item.icon }}</v-icon>
                    </v-list-item-action>
                    <v-list-item-content>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-navigation-drawer>

        <v-app-bar
            app
            clipped-left
            :color="colorFont">
            <v-app-bar-nav-icon @click.stop="drawer = !drawer" class="hidden-md-and-up"></v-app-bar-nav-icon>
            <v-toolbar-title>
                <b style="text-transform: uppercase;">{{ currentRouteName }}</b>
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-toolbar-items class="hidden-sm-and-down">
                <v-btn v-for="(item, i) in items" :key="i" :color="colorFont" depressed
                    @click="$vuetify.goTo(item.link, options)">
                    {{ item.title }}
                </v-btn>
            </v-toolbar-items>
        </v-app-bar>
	</div>
    
</template>


<script>
    export default {
		name: 'NavigationBar',

		computed: {
			currentRouteName() {
				return this.$route.name
			},
            options () {
                return {
                    duration: this.duration,
                    offset: this.offset,
                    easing: this.easing,
                }
            },
		},

		created () {
			this.$vuetify.theme.dark = true
		},

		props: {
			source: String,
		},

		components: {
		// HelloWorld,
		},

        data: () => ({
			fab: false,
			transition: 'slide-x-reverse-transition',
			drawer: false,
			items: [
				{ title: 'vue', icon: 'mdi-folder-open', link: '#vue-start', color:'black'},
                { title: 'feature', icon: 'mdi-folder-open', link: '#vue-feature', color:'black'},
                { title: 'project', icon: 'mdi-folder-open', link: '#vue-project', color:'black'},
                { title: 'card', icon: 'mdi-folder-open', link: '#vue-card', color:'black'},
                { title: 'contact', icon: 'mdi-folder-open', link: '#vue-contact', color:'black'},
			],
			route_now: '',
			colorFont: '',
            duration: 300,
            offset: 0,
            easing: 'easeInOutCubic',
        }),
    }
</script>