const state = {
	curentProduct: {
        name: '',
        src: '',
        color: '',
        fontColor: '',
    },
    dataProducts: [
        {
            name: 'pepsi',
            src: 'pepsi001.png',
            color: '#0161b8',
            fontColor: 'white',
        },
        {
            name: 'diet pepsi',
            src: 'pepsi002.png',
            color: '#0161b8',
            fontColor: 'white',
        },
        {
            name: 'pepsi zero sugar',
            src: 'pepsi003.png',
            color: '#0f1113',
            fontColor: 'white',
        },
    ],
};

const getters = {
	getCurentProduct: (state) => state.curentProduct,
	getDataProducts: (state) => state.dataProducts,
};

const actions = {
	setCurentProduct({ commit }, payload){
		commit('SET_CURENT_PRODUCT', payload)
	},
};

const mutations = {
	SET_CURENT_PRODUCT: (state, data) => state.curentProduct = data,
};

export default {
	state,
	getters,
	actions,
	mutations
}